import api from "@/consfig/api";


export default {
  async mounted() {},

  data: () => ({
    respuesta: "",
  }),

  computed: {},

  methods: {
    async ingresar() {
      console.log(this.email);
      await api
        .post("/usuario/" + this.email + "/" + this.passo, {})
        .then((response) => {
          if (response.status === 200) {
            this.respuesta = response.data;
            
            if (this.respuesta.nombre != null) {
              localStorage.setItem("id", this.respuesta.id);
              localStorage.setItem("tipoUsuario", this.respuesta.tipoUsuario);
          
              this.$router.push("/menu");
      
              this.$toast.success("USUARIO AUTENTICADO EXITOSAMENTE", {
                position: "bottom-center",
              });
            }else {
              this.$toast.error("Usuario no registrado", {
                 duration: 1000,
                position: "top-right",
              });
            }
            
          } else {
            this.$toast.error("ERROR AL Ingresar", {
               duration: 1000,
              position: "top-right",
            });
          }
        });
    },
  },
};
